A few notes:
1. The Worldcrunch logo + slogan are placeholders, for a general idea of where they will go when a new logo is designed.
2. Breaking News section is a full-width marquee going from right to left.
3. Fonts used: Archivo Narrow Bold and the Roboto family, header/navigation color: #242424, subtitle text: #7b7b7b
4. New orange: #e55d33
5. Hover over: For article title text, hovering over will change the font color from #242424 to #e55d33
    a. Hovering over articles below the "Prince" element will change the font color of the title the    same way, but also reverse the country color from #e55d33 to #242424
6. The Special Series bar is a slider, featuring one series at a time.
    a. When hovering over an article in the Special Series, the background darkens and the font color changes to #e55d33
7. Our sources and partners logos are also a full-width marquee going from right to left. 
8. Social media icons - are a placeholder as well, I've looked at the templates Glyphicon options but I don't know to what extend they can be altered in the template.
