/*Need to define the naming for the colors
if we should go by general color name or general color purpose (wc-orange vs wc-maincolor)*/
#article-background {
  background-color: #F8F8F8;
}
#article {
  background-color: #FFFFFF;
  border-radius: 9px;
  box-shadow: 2px -2px 20px #7B7B7B;
  border-width: medium;
}
#article-img {
  display: inline-block;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 0;
  padding-top: 72%;
}
#article-image-description {
  cursor: default;
  position: absolute;
  bottom: 0px;
  text-align: right;
  width: 100%;
  font-size: 20px;
  z-index: 40;
  color: white;
  background: rgba(54, 54, 54, 0.5);
}
#article-image-description:hover {
  background: rgba(54, 54, 54, 0);
}
.article-content {
  margin-left: 100px;
  margin-right: 100px;
}
#article-source {
  width: 100%;
  text-align: center;
}
#article-source-img {
  display: inline-block;
}
