@import "variables.less";

.navigation .active > a::after{
    background-color: rgba(0,0,0,0.0);
}

#topMain.nav-pills > li.active > a{
    color: @wc-orange;
}

#logo{
    float:left;
}

#slogan{
    float:right;
    margin-top: 15px;
}

.zocial, a.zocial{
    border: none;
}

.nav .social-glyphs{
    font-size: 12px;
}

/*icon color on hover*/
#header ul.nav-second-main li .fa-facebook:hover{
    color: @wc-fbicon-hover;
}
#header ul.nav-second-main li .fa-twitter:hover{
    color: @wc-twittericon-hover;
}

.li-subscribe{
    background-color: @wc-orange;
    padding: 20px 10px 20px 10px !important;
    top: -20px;
    margin-bottom: -20px;
}

#header ul.nav-second-main li > a{
    font-weight: bold;
}

