@import "variables.less";

@media (min-width: 993px) {
    /*General Body Stuff*/
    body{
        font-size: 20px;
    }

    P {
        font-size: 24px;
    }

    h3{
        font-size: 40px;
        line-height: 45px;
    }

    .tab-special{
        font-size: 18px;
    }

    .tab-special p{
        font-size: 18px;
    }

    .homepage-featured{
        display: inline-block;
        position: relative;
        background-size: cover;
        background-position:center; 
        background-repeat: no-repeat;
        width: 100%;
        height: 0;
        padding-top: 30%;
    }

    .homepage-featured-text{
        cursor: default;
        position: absolute;
        bottom: 50%;
        text-align: center;
        width: 100%;
        font-size: 50px;
        font-weight: bold;
        z-index: 40;
        color: @wc-white;
    }

    .homepage-featured-source{
        cursor: default;
        position: absolute;
        bottom: 60%;
        text-align: center;
        width: 100%;
        font-size: 40px;
        z-index: 40;
        color: @wc-white;
    }

    /*Carousel with source partners*/
    .owl-carousel .owl-wrapper-outer{
        padding-left: 40px;
    }

    /*Homepage image fixes*/
    .onepicture .hover{
        background-color: @wc-white-hover;
        max-width: 50%;
        min-height: 120px;
    }

    .onepicture .text-title{
        color: @wc-dark;
        font-size: 28px;
    }
}