@import "variables.less";


@media (max-width: 992px) {
    #header{
        height: 50px !important;
    }

    .homepage-featured{
        display: inline-block;
        position: relative;
        background-size: cover;
        background-position:center; 
        background-repeat: no-repeat;
        width: 100%;
        height: 0;
        padding-top: 72%;
        margin-top: 100px;
    }

    .homepage-featured-text{
        cursor: default;
        position: absolute;
        bottom: 100%;
        text-align: center;
        width: 100%;
        font-size: 30px;
        font-weight: bold;
        z-index: 40;
        color: @wc-darkgray;
        right: 0%;

    }

    .homepage-featured-source{
        cursor: default;
        position: absolute;
        bottom: 90%;
        text-align: center;
        width: 100%;
        font-size: 20px;
        z-index: 40;
        color: @wc-lightgray;
        right: 0%;

    }

    .li-subscribe{
        padding: 26px 5px 10px 5px !important;
    }

    /*Footer*/
    #subscribe-btn{
        padding-top: 0px!important;
    }
    
    .owl-carousel .owl-wrapper-outer{
        padding-left: 20px;
    }
}