/*Footer*/
#footer {
    background: @wc-dark;
    color: rgba(255, 255, 255, 0.6);
    font-size: 14px;
    font-family: 'Roboto', sans-serif !important; 

}

#footer h4{
    font-size: 18px;
    font-style: normal;
    font-weight: normal;
    color: @wc-orange;
}

#subscribe-btn{

    text-align: center;
    height: 35px;
    background: #121212;
    padding-top: 5px;
}
#subscribe-btn h4{
    font-weight:normal;
    font-size: 18px;

}

#footer > .container{
    padding-top: 20px;
    margin-bottom: 0px;
}

#footer .list-inside{
    list-style-type: none;
    font-size: 12px;
}

.footer-rights{
    color: @wc-orange;
}

.social-icons-footer{
    font-size: 30px !important;
}